import React, { Component } from "react";

export default class Map extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="walls">
                {this.props.board.map(x => <button className=
                    {x.type === "walkThrough" ? "wall passage" : "wall barrier"}>
                    {x.player === "isHere" && x.type === "walkThrough" ?
                        <p className="">{"\uD83D\uDC40"}</p> : ""}
                    {x.obj === "item" && x.type === "walkThrough" ?
                        <p className="">{"\uD83D\uDE21"}</p> : ""}
                    {x.obj === "lives" && x.type === "walkThrough" ?
                        <p className="">{"\uD83D\uDC9A"}</p> : ""}
                    {x.obj === "keys" && x.type === "walkThrough" ?
                        <p className="">{"\uD83D\uDD31"}</p> : ""}
                    {x.obj === "boss" && x.type === "walkThrough" ?
                        <p className="">{"\uD83D\uDC7F"}</p> : ""}
                </button>)}

            </div>
        )
    }
}


