export default function toggleDarkness(board, playerLocation, currentState) {
    let ShowThis = [];
    let arr = [];
    const currentMap = board
    const player = playerLocation;
    const logo = { x: player.x, y: player.y }
    const top = { x: player.x - 1, y: player.y }
    const bottom = { x: player.x + 1, y: player.y }
    const right = { x: player.x, y: player.y - 1 }
    const left = { x: player.x, y: player.y + 1 }
    const bottomLeft = { x: player.x + 1, y: player.y - 1 }
    const bottomRight = { x: player.x + 1, y: player.y + 1 }
    const topLeft = { x: player.x - 1, y: player.y - 1 }
    const topRight = { x: player.x - 1, y: player.y + 1 }
    ShowThis.push(topLeft, top, topRight, bottom, right, logo, left, bottomLeft, bottomRight, );
    for (let path of ShowThis) {
        let paths = board.find(x => x.x === path.x && x.y === path.y);
        if (paths !== undefined) {
            arr.push(paths)
        }
    }
    const sorteArr = arr.sort((a, b) => {
        if (a.x > b.x) return 1;
        if (a.x < b.x) return -1;
        if (a.y > b.y) return 1;
        if (a.y < b.y) return -1;
        return a.x > b.x ? 1 : -1;
    })
    console.log(sorteArr)

    var got = { currentState: !currentState, surroundings: sorteArr };
    return got;
}


