export function createBoardAction(board) {
    console.log("board", board)
    return {
        type: "CHANGE_BOARD", value: board
    }
}
export function createPlayerAction(player) {
    return {
        type: "ADD_PLAYER", value: player
    }
}
export function toggleDarknessAction(darkness) {
    return {
        type: "TOGGLE_DARKNESS", value: darkness
    }
}




