export default function usersReducer(state = { arr: [], obj: "" }, action) {
    switch (action.type) {
        case "UPDATE_USER":
            return { ...state, arr: "dlfjasld" }
            break;
        case "CHANGE_BOARD":
            return { ...state, board: action.value }
            break;
        case "TOGGLE_DARKNESS":
            return { ...state, board: action.value }
            break;
    }
    return state;
}


